@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities"; 


body {
    font-family: "Space Grotesk", serif;
    margin: 0;
    padding: 0;
    background: #1d2936;
    color: #333;
}


.bg-header {
    background-color: #2c3e50;
}

.glow-white {
    text-shadow: 0 0 7px #ffffff;
}

.glow-black {
    text-shadow: 0 0 7px #000000;
}

.glow-red {
    text-shadow: 0 0 7px #ff0000;
}

.glow-orange {
    text-shadow: 0 0 7px #ff7b00;
}

.glow-yellow {
    text-shadow: 0 0 7px #ffdd00;
}

.glow-green {
    text-shadow: 0 0 7px #15ff00;
}

.glow-blue {
    text-shadow: 0 0 7px #0800ff;
}

.glow-purple {
    text-shadow: 0 0 7px #930087;
}
  